*{
    margin: 0px;
    padding: 0px;
}
a{
    text-decoration: none;
}
.nav{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.nav-background{
    position: absolute;
    left: 0px;
    width: 100%;
    height: 100%;
    background: linear-gradient(199deg, rgba(0, 0, 0, 0.00) 45.31%, rgba(0, 0, 0, 0.84) 67.37%);
}
.main-image{
    opacity: 90%;
    width: 100%;
    height: 100%;
}
.nav-bar{
    width: 100%;
    height: 100px;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50%;
}
.main-logo{
    width: 20.88px;
    height: 42.92px;
    z-index: 20;
}
.nav-links{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    list-style: none;
    z-index: 10;
}
.nav-link{
    color: #FFF;
    font-family: 'Cairo', sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.377px;
}
.nav-btn{
    border-radius: 0px 18px 0px 0px;
    background: #FFAC12;
    width: 211px;
    height: 50px;
    flex-shrink: 0;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

/* 1100px Change */
@media (max-width:1100px) {
    .nav{
        position: relative;
        width: 100%;
        height: 70vh;
        display: flex;
        justify-content: center;
        align-items: flex-start;
    }
    .nav-bar{
        width: 90%;
        height: 100px;
        margin-top: 30px;
        gap: 60%;
    }
    .main-logo{
        width: 10.88px;
        height: 32.92px;
    }
    .nav-links{
        gap: 30px;
        z-index: 10;
    }
    .nav-link{
        font-size: 12px;
    }
    .nav-btn{
        width: 111px;
        height: 40px;
        gap: 10px;
    }
}
@media (max-width:900px){
    .nav{
        height: 40vh;
    }
    .main-image{
        width: 100%;
        height: 100%;
    }
}
.nav-bar-small {
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    width: 100%;
    height: 50px;
    gap: 80%;
}
.main-logo-small{
    width: 10px;
    height: 30px;
}
#nav-btn{
    color: #FFAC12;
    font-size: 20px;
}
