/* ------------ */
.features{
    background: #E4E6F1;
    width: 100%;
    height: 100vh;
    gap: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.features-image{
    width: 50%;
    height: fit-content;
    border-radius: 40px;
    flex-shrink: 0;
}
.features-content{
    height: 50%;
    gap: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.features-content-tag{
    color: #000;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px; /* 140% */
    letter-spacing: -0.503px;
    color: #000;
    font-family: 'Cairo', sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px; /* 140% */
    letter-spacing: -0.503px;
}
.border-text{
    background:linear-gradient(270deg ,  #000000 27%, #FFAC12 67%);
    width: 80%;
    height: 3px;
    margin-bottom: 30px;
}
.features-content-title{
    color: #000;
    text-align: right;
    font-size: 50px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -1.256px;
    margin-bottom: 150px;
}

@media (max-width:800px) {
    .features-content-title{
        font-size: 35px;
    } 
}
@media (max-width:600px) {
    .features{
        height: 50vh;
        gap: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .features-image{
        display: none;
    }
    .features-content{
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .features-content-title{
        font-size: 40px;
        margin-bottom: 50px;
    }
}
